.LinksList {
    text-decoration: none;
    margin-right: 1rem;
    width: 22%;

    @media (max-width: 900px) {
        width: 40%;
    }
    @media (max-width: 750px) {
        width: 90%;
    }


    ul {
        list-style: none;
        padding-left: 0;
    }

    h3 {
        color: #74698C;
        text-transform: uppercase;;
    }

    .item {
        margin-right: 1rem;
        margin-bottom: 1rem;
    }

    .link {
        transition: all 200ms ease-in-out;
        font-size: 20px;
        line-height: 21px;
        color: #AEAEAE;

        &:hover {
            color: #A31FED;
    
           
          }
        
    }



 














}