
    .item {
        margin-right: 1rem;
        margin-bottom: 1rem;
    }

    .link {
        display: block;
        transition: all 200ms ease-in-out;
        font-weight: bold;
        font-size: 26px;
        line-height: 21px;
        color: black;
        cursor: pointer;
    }

    .link:hover {
      color: #A31FED;
    }

    
    .header {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding-left: 0;
        align-items: center;

        @media (max-width: 1450px) {
            .item {
                display: none;
            }
           
        }

        
    }

    .sidebar {
        display: flex;
        flex-direction: column;
        list-style: none;

        .item {
            margin-bottom: 1rem;
        }
    }

    .button {
        display: block;
    }











