.HomePage {

    display: flex;
    transition: all 0.2s ease-in-out;


    @media (max-width: 600px) {
        flex-direction: column;
    }


    .image {
        width: 100%;
        opacity: 0.7;
        transition: all 0.2s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }

    .imageBlock {
        position: relative;
        width: 50%;
        cursor: pointer;

        @media (max-width: 600px) {
            width: 100%;
        }
    }

    .blockTitle {
        position: absolute;
        font-size: 3rem;
        font-weight: bold;
        text-align: center;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        z-index: 1;
        pointer-events: none;

        @media (max-width: 1600px) {
            font-size: 2rem;
        }
    }

    .whiteText {
        color: white;
    }

    








    
}