.PreFooter {



    .linksLists {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 1rem;
    }

    .socialVisa {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        padding: 1rem;
    }

    .visaMaster {
        width: 430px;
        margin-top: 1rem;


        @media (max-width: 450px) {
            width: 350px;
        }
    }

    .googleApp {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .topPrefooter {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        padding: 1rem;


        @media (max-width: 920px) {
            justify-content: center;
        }
    }

    .googleAppItem {
        margin-right: 1rem;
        margin-bottom: 1rem;
    }

    .prefooterBGBlock {
        position: relative;
        margin-bottom: 5rem;
    }

    .prefooterBG {
      width: 100%;
      margin-bottom: 2rem;

      @media (max-width: 600px)  {
          margin-bottom: 3rem;
      }
    }

    .roundBlock {
        display: flex;
        justify-content: center;
        align-content: center;
        align-items: center;
        position: absolute;
        top: 5%;
        left: 90px;
        border-radius: 50%;
        background-color: #807CFA;
        width: 300px;
        height: 300px;

        // @media (max-width: 900px) {

        // }

    }

    .texts {
        text-align: center;
    }

    .whiteText {
        color: white;
        font-size: 1.2rem
    }

    .blackText {
        font-weight: bold;
        font-size: 1.2rem;
    }

    .greenButton {
        position: absolute;
        width: 290px;
        bottom: 0;
        left: 30px;
    }

    @media (max-width: 3000px) {
        .roundBlock {
            width: 350px;
            height: 350px;
        }
        .greenButton {
            bottom: 50px;
            left: 100px;
        }
    }

    @media (min-width: 1300px) {
        .roundBlock {
            top: 20%;
        }
        .greenButton {
            bottom: 50px;
            left: 100px;
        }
    }


    @media (max-width: 1100px) {
        .roundBlock {
            width: 250px;
            height: 250px;
        }
        .whiteText, .blackText  {
            font-size: 1rem;
        }
        .greenButton {
            bottom: 15px;
        }
    }

    @media (max-width: 700px) {
        .roundBlock {
            width: 250px;
            height: 170px;
            left: 55px;
        }
        .greenButton {
            bottom: -25px;
            left: -15px;
        }
    }


}