.Button {
    padding: 1rem 2rem;
    font-size: large;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.2s ease-out;
    outline: none;

    


}

.purple {
    color: #A31FED;
    border-color: #5C0BFF;
    background-color: transparent;

    &:hover {
        background-color: #A31FED;
        color: white;
    }

}

.green {
    background-color: #3ADF54;
    border: none;

    &:hover {
        background-color: #A31FED;
        color: white;
    }
}

.purpleBG {
    color: white;
    border-color:#A31FED;
    background-color: #A31FED;

    &:hover {
        background-color: transparent;
        color: #A31FED;
    }
}

.violetBG {
    color: white;
    border-color: #5C0BFF;
    background-color: #5C0BFF;

    &:hover {
        background-color: transparent;
        color: #5C0BFF;
    }

}